<template>
  <div class="hello">

    <el-container :direction="vertical">
      <el-header class="tableheader" height="60">
        <el-row :gutter="20" class="padd-height">
          <el-col :span="22" :offset="0" type="flex" align='center' class="padd-height">
            <div class="logo" :class="{'w200':!isCollapse}" title="DM工具箱" @click="isCollapse=!isCollapse">
              <i class="el-icon-s-shop"> </i>DM
            </div>
            <el-menu mode="horizontal" class="navmenu" background-color="#545c64" text-color="#fff" active-text-color="#ffd04b" :default-active="activeNavIndex+''" @select="handleNavSelect">
              <el-menu-item :index="index+''" v-for="(item, index) in navList" :key="index">{{item.meta.title}}</el-menu-item>
            </el-menu>
          </el-col>
        </el-row>
      </el-header>
      <el-container>
        <el-aside width="auto">
          <!-- Aside content -->
          <!-- <div class="autoicon" @click="isCollapse=!isCollapse" :class="[isCollapse?'':'w200']">
            <span class="el-icon-suitcase"></span>
          </div> -->
          <!-- <el-radio-group v-model="isCollapse" class="autoicon">
            <el-radio-button :label="false">
              <span class="el-icon-suitcase"></span>
            </el-radio-button>
            <el-radio-button :label="true">
              <span class="el-icon-suitcase"></span>
            </el-radio-button>
          </el-radio-group> -->
          <el-menu default-active="1" class="el-menu-vertical-demo" @open="handleOpen" @close="handleClose" :collapse="isCollapse" @select="handleSideBarSelect" background-color="#545c64" text-color="#fff" active-text-color="#ffd04b">
            <el-menu-item index="1" v-for="(item,index) in menuList" :key="index">
              <i class="el-icon-copy-document"></i>
              <span slot="title">{{item.meta.title}}</span>
            </el-menu-item>
          </el-menu>
        </el-aside>
        <el-container :direction="vertical">
          <el-main height="" class="main">
            <!-- Main content -->
            <!-- 表单编辑 -->
            <div class="hasmenu">
              <section class="main-area">
                <header class="header-back">
                  <!-- <el-button type="primary" size="default" @click="handleBack">返回</el-button> -->
                  <div class="btn-click">
                    <el-tag effect="plain" class="back" @click="handleBack()">
                      <i class="el-icon-d-arrow-left">
                        返回
                      </i>
                    </el-tag>
                  </div>
                </header>
                <transition name="el-fade-in-linear">
                  <router-view class="router-view"> </router-view>
                </transition>
              </section>
            </div>
          </el-main>
          <el-footer height="">
            <!-- Footer content -->
            <!-- Footer -->
          </el-footer>
        </el-container>
      </el-container>
    </el-container>

  </div>
</template>

<script>
  export default {
    name: 'index',
    data() {
      return {
        activeMenuIndex: 0,
        navList: this.$router.options.routes.filter(item => item.meta && item.meta.nav),
        activeNavIndex: 0,
        vertical: 'vertical',
        isCollapse: false,
        menuList: [],
      }
    },
    watch: {
      isCollapse(value) {
        this.$forceUpdate();

      },
      '$route'(value) {
        // console.log(value)
        // this.matched = value.matched
      }

    },
    mounted() {
      // console.log(this.menuList)
      this.$nextTick(() => {
        this.getMenuList()
      })
    },
    methods: {
      getMenuList() {
        let routes = this.$router.options.routes;
        let matched = this.$route.matched.filter(item => item.path);
        if (matched.length === 0) return []
        let currentName = matched[0].name;
        let route = routes.find(item => item.name == currentName);
        this.menuList = route.children.filter(item => item.meta && item.meta.isshow)
        //默认被选中
        this.activeNavIndex = routes.filter(item => item.meta && item.meta.nav).findIndex(item => item.name == currentName)
      },
      handleNavSelect(index) {
        this.activeMenuIndex = index;
        let item = this.navList[index]
        this.$router.push({
          path: item.path
        })
        this.$nextTick(() => {
          this.getMenuList()
        })
      },
      handleSideBarSelect(index) {
        let item = this.menuList[index - 1]
        this.$router.push({
          path: item.path
        })
      },
      handleBack() {
        console.log(this.$route)
        this.$router.back()
      },
      handleClose(key, path) {
        console.log(key, path)
        // 关闭导航栏
      },
      handleOpen(key, path) {
        console.log(key, path)
      }


    }
  }
</script>

<style lang='less' scoped>
  .el-menu-vertical-demo:not(.el-menu--collapse) {
    width: 200px;
    min-height: 600px;
  }

  .main {
    padding: 10px;
  }

  .header-back {
    .btn-click {
      .back {

        cursor: pointer;
        color: #409EFF;

        &:hover {
          color: #fff;
          background: #409EFF;
        }
      }

      // float: left;


    }
  }

  .el-menu--collapse {
    min-height: 600px;

  }

  .navmenu {
    padding-left: 200px;
  }

  .w200 {
    // width: 200px !important;
  }

  .router-view {
    padding: 25px;
    min-height: 300px;
  }

  .autoicon {
    position: relative;
    z-index: 111;
    display: flex;
    justify-content: center;

    span {

      width: 40px;
      height: 40px;
      font-size: 20px;
      display: flex;
      align-items: center;
      justify-content: center;
      color: #fff;
    }

    background: rgb(233, 95, 101);
  }

  .form-list {
    // padding: 10px 30px;
    // padding-top: 20px;
    height: 60px;
    position: relative;
    background: #fff;
    z-index: 10;

  }

  .main-area {
    // padding-top: 120px;
    position: relative;
  }

  .big-btn {
    display: block;
    margin: 150px auto;
    width: 200px;
    height: 70px;
    font-size: 20px;
  }

  .tableheader {
    // background: rgb(64, 158, 255);
    // color: #fff;
    padding: 0;

  }

  .padd-height {
    line-height: 60px;
  }

  .el-header {
    background: rgb(84, 92, 100);
  }

  .form-area {
    float: left;
    background: #fff;

    .form-btn {
      margin-right: 10px;
      margin-bottom: 6px;
    }
  }


  .logo {
    width: 64px;
    text-align: center;
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100px;

    float: left;
    color: #fff;
    z-index: 9;
    font-size: 22px;
    position: relative;

    &:hover {
      cursor: pointer;
      color: aqua;

      i {
        color: aqua;
      }
    }

  }


  .edit-btn {
    background: #fff;
  }

  .btn-right {

    .delete {
      // padding: 5px;
      border-radius: 5px;
      cursor: pointer;
      opacity: 0.8;

      &:hover {
        opacity: 1;

      }

    }

  }


  .container {
    height: calc(100vh - 60px);
  }
</style>


